  async callLoginAPI(dbcode: string) {
    this.saveItem('Db_code', 'null');
    this.saveItem('Db_code', dbcode.trim());
    // this.getCountryList() ;
    if (this.email != '' || this.password != '') {
      let loading = await this.loadingComp(this.loadingCtrl);
      loading.present();

      
      
        let url =
        this.BASE_URL +
        'api/servicesAPI/validateuser_mob?UserName=' +
        this.email +
        '&pword=' +
        this.password +
        '&Key=' +
        this.key +
        '&con=' +
        this.Dbcode;
   
        let  url_enc =
        this.BASE_URL +
        'api/servicesAPI/validateuser_mob?UserName=' +
        this.email +
        '&pword=' +
        this.Encrypt.EncryptTextWithPublicKey(this.password,this.AesSecretIVKey)+
        '&Key=' +
        this.key +
        '&con=' +
        this.Dbcode;
    

     


        

        

      // var url = this.LOGIN_URL+"?UserName="+this.email+"&Pword="+this.password ;

      let postData = new FormData();


      this.http.post(url, postData).subscribe(
        async (result) => {
          console.log(result,"result")
          try {
            var parsedJSON = JSON.parse(JSON.stringify(result));
            if (parsedJSON[0].UserId != 0) {
              this.saveItem(this.LOGIN_USER_DATA, JSON.stringify(result));
              this.saveItem(this.IS_LOGIN, '1');
              this.saveItem('Db_code', this.Dbcode.trim()); //store the customercode/connectionstring to local storage
              // this.saveItem('Db_code', "ConnectionString") ;

              //Saving Tokens Here
              this.TokenService.setToken(result[0].token)
               this.TokenService.setRefreshAccessToken(result[0].refreshToken)
               this.TokenService.setAuthToken(result[0].refreshAccessToken) 

              this.sendDeviceToken(parsedJSON[0].UserId);
              await this.callOrightPrefernceApi(parsedJSON[0]);
              await this.callDairyFarmConfigurationApi(parsedJSON[0].UserId);
              await this.callDairyLogoPathForCoobrandingApi(
                parsedJSON[0].Farm_ID
              );
              loading.dismiss();
              await this.GetServices(parsedJSON, parsedJSON[0].UserId);

              this.dbProvider.createAndInsertUserTable();
              this.getUserNotifications();
              this.Auth.startPasswordCheckService(true)
              this.events.publish('login_done', ''); // this is the main event which call the login
            } else {
              loading.dismiss();
              this.presentToast(this.toastCtrl, 'Invalid login credentials');
            }
          } catch (err) {
            loading.dismiss();
            this.presentToast(this.toastCtrl, 'Invalid Credentials');
          }
        },
        (error) => {
          loading.dismiss();
          console.error('API Error', error);
          this.somethingWentWrongError(this.toastCtrl);
        }
      );
    } else {
      this.presentToast(this.toastCtrl, 'Please enter fields properly');
      this.saveItem('Db_code', '');
    }
  }

  getUserNotifications() {
    let lastrecord_id = 0;
    this.dbProvider.getAllNotificationRecords(this.getFarmerUserID()).then(
      (res: any) => {
        // if (res.rows.length > 0) {//##if farmer not exist
        //   lastrecord_id = res.rows.item(res.rows.length - 1).ID ;
        // }
        if (res.rows.length == 0) {
          this.callNotificationsApi(lastrecord_id);
        } else {
          for (let i = 0; i < res.rows.length; i++) {
            if (lastrecord_id < res.rows.item(i).ID) {
              //##if farmer not exist
              lastrecord_id = res.rows.item(i).ID;
            }

            if (i == res.rows.length - 1) {
              this.callNotificationsApi(lastrecord_id);
            }
          }
        }
      },
      (error) => {
        console.error('getNotificationList error: ', error);
      }
    );
  }
  callNotificationsApi(lastrecord_id) {
    let url =
      this.BASE_URL +
      'api/servicesAPI/getAllUserNotifications?userid=' +
      this.getFarmerUserID() +
      '&lastrecod_id=' +
      lastrecord_id +
      '&Key=' +
      this.key +
      '&con=' +
      this.Dbcode;

    this.http.get(url).subscribe(
      (result) => {
        //loading.dismiss() ;
        try {
          var parsedJSON = JSON.parse(JSON.stringify(result));
          if (parsedJSON.length > 0) {
            //##only execute if api json data length is more than 0
            for (let i = 0; i < parsedJSON.length; i++) {
              if (parsedJSON[i].Notifications_id != 0) {
                const NotificationApilist: NotificationApi = {
                  Notifications_id: parsedJSON[i].Notifications_id,
                  Notification_Title: parsedJSON[i].Notification_Title,
                  Notification_Body: parsedJSON[i].Notification_Body,
                  Notification_Data: parsedJSON[i].Notification_Data,
                  Notification_Date: parsedJSON[i].Notification_Date,
                  Notification_Seen: parsedJSON[i].Notification_Seen,
                };
                this.dbProvider.createAndInsertNotificationTable(
                  NotificationApilist.Notifications_id,
                  this.getFarmerUserID(),
                  NotificationApilist.Notification_Title,
                  NotificationApilist.Notification_Body,
                  NotificationApilist.Notification_Data,
                  NotificationApilist.Notification_Seen,
                  NotificationApilist.Notification_Date
                );

              
              }
            }
          }
          try {
            // this.getFailedCC() ;
          } catch (e) {
            console.error(e);
            //    this.presentToast(this.toastCtrl, "No Data Found") ;
          }
        } catch (e) {
          console.error(e);
        }
      },
      (error) => {
        //loading.dismiss() ;
        console.error('API Error', error);
        //    this.somethingWentWrongError(this.toastCtrl) ;
      }
    );
  }

