  OrightPreferences: OrightPreferences[] = [];

  callOrightPrefernceApi(json) {
    // let loading = this.loadingComp(this.loadingCtrl) ;
    //loading.present() ;

    return new Promise<void>((resolve, reject) => {
      try {
        var Organization_id = '0';
        if (json.userType == '3') {
          Organization_id = json.CollectionCentre_ID;
        } else if (json.userType == '7') {
          Organization_id = json.ChillingCentre_ID;
        } else if (json.userType == '2') {
          Organization_id = json.Farm_ID;
        }
        let details = [
          {
            Organization_id: Organization_id,
            Usertype_id: json.userType,
            ParameterKey: '0',
          },
        ];

        let url =
          this.BASE_URL +
          'api/servicesAPI/getOrightPreferences?getOrightPreferences=' +
          JSON.stringify(details) +
          '&Key=' +
          this.key +
          '&con=' +
          this.Dbcode;

        this.http.get(url).subscribe(
          (result) => {
            //loading.dismiss() ;
            try {
              var parsedJSON = JSON.parse(JSON.stringify(result));
              if (parsedJSON.length > 0) {
                //##only execute if api json data length is more than 0
                for (let i = 0; i < parsedJSON.length; i++) {
                  const OrightPreferences: OrightPreferences = {
                    Organization_id: parsedJSON[i].Organization_id,
                    OrightPreference_id: parsedJSON[i].OrightPreference_id,
                    ParameterKey: parsedJSON[i].ParameterKey,
                    ParameterValue: parsedJSON[i].ParameterValue,
                    Usertype_id: parsedJSON[i].Usertype_id,
                    UserEditableStatus: parsedJSON[i].UserEditableStatus,
                    IsBoolean: parsedJSON[i].IsBoolean,
                    ShowToUserStatus: parsedJSON[i].ShowToUserStatus,
                    ParameterName: parsedJSON[i].ParameterName,
                  };
                  this.OrightPreferences.push(OrightPreferences);
                
                }
              
                this.saveItem(
                  'OrightPreferences',
                  JSON.stringify(this.OrightPreferences)
                );
              } else {
                this.saveItem(
                  'OrightPreferences',
                  JSON.stringify(this.OrightPreferences)
                );
              }
            } catch (e) {
              console.error(e);
            }
          },
          (error) => {
            //loading.dismiss() ;
            console.error('API Error', error);
            // this.somethingWentWrongError(this.toastCtrl) ;
          }
        );
        resolve();
      } catch (e) {
        console.error('error function callOrightPrefernceApi LoginPage', e);
      }
    });
  }

  DairyFarmConfiguration: DairyFarmConfiguration[] = [];
  callDairyFarmConfigurationApi(userid) {
    return new Promise<void>((resolve, reject) => {
      try {
        var url =
          this.BASE_URL +
          'api/DairyFarmApi/getAllDairyFarmConfiguration?UserId=' +
          userid +
          '&Key=' +
          this.key +
          '&con=' +
          this.Dbcode;
        let postData = new FormData();


        this.http.get(url).subscribe((result) => {
          try {
            var parsedJSON = JSON.parse(JSON.stringify(result));

            if (parsedJSON.length > 0) {
              //##only execute if api json data length is more than 0
              for (let i = 0; i < parsedJSON.length; i++) {
                const configuration: DairyFarmConfiguration = {
                  UserId: parsedJSON[i].UserId,
                  FarmConfiguration_ID: parsedJSON[i].FarmConfiguration_ID,
                  Farm_Name: parsedJSON[i].Farm_Name,
                  Parameter: parsedJSON[i].Parameter,
                  Value: parsedJSON[i].Value,
                };
                this.DairyFarmConfiguration.push(configuration);
             
              }
              
              this.saveItem(
                'DairyFarmConfiguration',
                JSON.stringify(this.DairyFarmConfiguration)
              );
            } else {
              this.saveItem(
                'DairyFarmConfiguration',
                JSON.stringify(this.DairyFarmConfiguration)
              );
            }
          } catch (err) {
            console.error(err);
          }
        });
        resolve();
      } catch (e) {
        console.error(
          'error function callDairyFarmConfigurationApi LoginPage:- ',
          e
        );
      }
    });
  }

  dairyLogoPathForCoobranding: DairyLogoPathForCoobranding[] = [];
  callDairyLogoPathForCoobrandingApi(dairyId) {
    return new Promise<void>((resolve, reject) => {
      try {
        var url =
          this.BASE_URL +
          'api/DairyFarmApi/getDairyLogoPathForCoobranding?DairyID=' +
          dairyId +
          '&Key=' +
          this.key +
          '&con=' +
          this.Dbcode;
        let postData = new FormData();


        this.http.get(url).subscribe((result) => {
          try {
            var parsedJSON = JSON.parse(JSON.stringify(result));

            if (
              parsedJSON.length > 0 &&
              parsedJSON[0].DairyFarm_Id != '0' &&
              parsedJSON[0].Farm_Logo_Path != 'null'
            ) {
              //##only execute if api json data length is more than 0
              this.dairyLogoPathForCoobranding = parsedJSON;
             
              this.saveItem(
                'Farm_Logo',
                JSON.stringify(
                  this.dairyLogoPathForCoobranding[0].Farm_Logo_base64
                )
              );
              
              this.saveItem(
                'Farm_Logo_Path',
                JSON.stringify(
                  this.dairyLogoPathForCoobranding[0].Farm_Logo_Path
                )
              );
            } else {
              this.saveItem('Farm_Logo_Path', 'null');
              this.saveItem('Farm_Logo', 'null');
            }
          } catch (err) {
            console.error(err);
          }
        });
        resolve();
      } catch (e) {
        console.error(
          'error function callDairyLogoPathForCoobrandingApi LoginPage:-',
          e
        );
      }
    });
  }

